		connectSocket:function() {
			this.lastHeartBeat = new Date().getTime() 
			this.socketTask = uni.connectSocket({
				url: 'ws://192.168.31.150:8700/posServer/websocket/1',
				success: ()=> {}
			});
			this.socketTask.onMessage(function(res) {
