			});
		},
		checkConnect: function() {
			this.socketTask.send({
				data: "{cmd:100,token:'"+this.token+"'}"
			});
			if ((new Date().getTime() - this.lastHeartBeat) > this.overtime) {
				console.log("socket 连接断开，正在尝试重新建立连接");
				this.connectSocket();
			}
		},
